<template>
	<view class="page">
		 <view class="" 
		 style="min-height: 650upx;position: relative;"
		 >
		    <view class=""
		    :style="'z-index: -2;position: absolute;top:0;background: url('+ActivityList.goods_master_image+') no-repeat;background-size: cover;width:100%;height: 100%;'"
		    >
			</view>
		   <view class="bg-gradual-green5" style="opacity:0.79;position: absolute;top:0;z-index: -1;width:100%;height:100%;">
			</view>	
			
			 <view class="padding" style="z-index: 30000;" :style="[{'padding-top':CustomBar + 'px'}]">
				 <navigator url="../notice/list" class="margin-right round padding-xxs" style="border-radius: 20px;padding: 10upx;float: right;margin-top: 40upx;background-color:rgba(28, 84, 73, 0.21);position: relative;">
				 	<text class="lg cuIcon-notificationfill text-white" style="font-size: 50upx;"></text>
					<view class="re-tag" v-if="noticeCount>99">99+</view>
					<view class="re-tag" v-if="noticeCount<=99">{{noticeCount}}</view>
				 </navigator>
				<!-- <view style="margin-top: 120upx;z-index: 30000;" v-if="isEmpty(ActivityList)">
					<view class="text-xxl text-white text-bold">2020<text class="cuIcon-title"></text>云书展</view> 
					<view class="text-df" style="color:#f1f1f1;">爱读书<text class="cuIcon-title"></text>爱生活</view> 
				</view> -->
				<view style="margin-top: 120upx;z-index: 30000;">
					<view class="text-xxl text-white text-bold">{{ActivityList.name||''}}</view> 
					<view class="text-df" style="color:#f1f1f1;">{{ActivityList.sponsor||''}}
					</view> 
				</view>
				<view class="cu-bar search margin-top">
					<view class="search-form round" style="margin:0;background:#00000033;height:80upx;">
						<text class="cuIcon-search text-white text-bold" style="font-size: 42upx;"></text>
						<input type="text" style="color:#FFFFFF;" disabled="" placeholder-style="color:#f1f1f1" placeholder="搜索书名" confirm-type="search" @tap="toSearch"></input>
					</view>
				</view>
				<view class="cu-list grid col-4 no-border margin-top-sm" style="background: unset;">
					<view data-url="../activity/hall" @tap="toUrl" class="cu-item align-center">
						<view class='cu-avatar lg round' :style="'background-color:unset;background-image: url('+ysz_ico_actsch+');'">
						</view>
						<text style="color:#f1f1f1;">展厅导览</text>
					</view>
					<view data-url="../activity/index" @tap="toUrl" class="cu-item align-center">
						<view class='cu-avatar lg round' :style="'background-color:unset;background-image: url('+ysz_ico_fbook+');'">
						</view>
						<text style="color:#f1f1f1;">活动日程</text>
					</view>
					<view data-url="./live" @tap="toUrl" class="cu-item align-center">
						<view class='cu-avatar lg round' :style="'background-color:unset;background-image: url('+ysz_ico_guide+');'">
						</view>
						<text style="color:#f1f1f1;">线上直播</text>
					</view>
					<view data-url="../classify/list" @tap="toUrl" class="cu-item align-center">
						<view class='cu-avatar lg round' :style="'background-color:unset;background-image: url('+ysz_ico_liveon+');'">
						</view>
						<text style="color:#f1f1f1;">我要找书</text>
					</view>
				</view>
			</view>
			<view style="border-radius: 30upx 30upx 0 0;background-color: #F9F9F9;z-index: 999;position: absolute;bottom:-340upx;left:0;width:100%;">
				<swiper class="screen-swiper margin" :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true" :circular="true" :current="current"
							 :autoplay="true" interval="5000" duration="500" indicator-color="#8799a3"
				indicator-active-color="#333333" @change="swiperChange" style="min-height: 160upx;height: 320upx;">
					<swiper-item v-for="(item,index) in swiperList" :key="index" @tap="adUrl" :data-advtype="item.adv_type" :data-advlink="item.adv_link" :data-id="item.id">
						<image :src="item.adv_image" mode="aspectFill" style="height:260upx;border-radius: 20upx;"></image>
					</swiper-item>
				</swiper>
			</view>
		</view>
		<view style="height: 320upx;"></view>
		<view style="background-color: #F9F9F9;">
			
			<view class="cu-bar margin-left">
				<view>
					<text class="lg text-xxl text-black text-bold">推荐图书</text>
				</view>
				<navigator url="../classify/list" class="action">
					<text class="text-gray3 text-df">全部</text><text class="sm cuIcon-right text-gray2"></text>
				</navigator>
			</view>
			<scroll-view scroll-x class="nav padding-lr" scroll-with-animation :scroll-left="scrollLeft">
				<view class="cu-item" style="height: 90upx;margin:0;padding: 0 10upx;" v-for="(item,index) in classList" :key="index" @tap="tabSelect" :data-id="index">
					<view class="cu-tag round padding-lr-sm" style="height:60upx;" :class="index==TabCur?'bg-gradual-green2':'bg-grey2'">{{item.name}}</view>
				</view>
			</scroll-view>
			<view class="margin bg-white" style="border-radius: 10upx;">
				<view class="grid col-2">
					<view :data-url="'../classify/detail?id='+item.id" @tap="toUrl" class="padding-sm margin-top" v-for="(item,index) in bookList" :key="index">
						<view class="text-center">
							<view class="cu-avatar xl book-shadow" :style="'width:200rpx;height: 260rpx;background-image:url('+item.goods_master_image+')'"></view>
						</view>
						<view class="text-black text-bold text-xl margin-top-sm">
							{{item.goods_name}}
						</view>
						<view class="text-gray2 text-sm margin-tb-xs">作者：{{item.author}}</view>
						<view>
							<view class='cu-tag radius bg-yellow2 '>精装</view>
							<view class='cu-tag radius bg-mauve2' v-if="!isEmpty(item.class)">{{item.class.name}}</view>
						</view>
					</view>
				</view>
				<view @tap="changeBook" class="cu-load text-gray2 margin-top-sm" style="border-top:1upx solid #EEEEEE;" :class="bookLoad"></view> 
			</view>
			<view class="cu-bar margin-left" style="min-height: unset;">
				<view>
					<text class="lg text-xxl text-black text-bold">线上直播</text>
				</view>
				<navigator url="./live" class="action">
					<text class="text-gray3 text-df">全部</text><text class="sm cuIcon-right text-gray2"></text>
				</navigator>
			</view>
			<view class="cu-card case">
				<view class="cu-item shadow" v-for="(item,index) in liveList" :key="index">
					<view :data-url="'./livedetail?id='+item.id" @tap="toUrl" class="image">
						<image :src="item.goods_master_image"
						 mode="widthFix"></image>
						<view v-if="item.live_status==1" class="cu-tag bg-gradual-red2" style="top:10upx;left:10upx;right:unset;">直播中</view>
						<view v-if="item.live_status!=1" class="cu-tag bg-gradual-blue2" style="top:10upx;left:10upx;right:unset;">回放</view>
						<view class="cu-bar bg-shadeBottom"></view>
						<view style="position: absolute;top:50%; left:50%; margin-top: -64upx;;margin-left: -64upx;">
							<view class="cu-avatar xl round" :style="'background-color:unset;background-image: url('+ysz_liv_play+');'">
								
							</view>
						</view>
					</view>
					<view class="padding-sm">
						<view class="text-black text-xl text-bold">{{item.goods_name}}</view>
						<view class="text-grey2 text-sm">主播：{{item.live_name}}</view>
					</view>
					<view v-if="index==liveList.length-1" @tap="changeLive" class="cu-load text-gray2 margin-top-sm" style="border-top:1upx solid #EEEEEE;" :class="liveLoad"></view>
				 
				</view>
			</view>
			<!-- <view class="cu-bar margin-left" style="min-height: unset;">
				<view>
					<text class="lg text-xxl text-black text-bold">猜您喜欢</text>
				</view>
			</view>
			
			<scroll-view scroll-x class="nav" scroll-with-animation>
				<view :data-url="'../classify/detail?id='+item.id" @tap="toUrl" class="padding-left-sm margin-tb" style="display: inline-block;" v-for="(item,index) in likeBookList" :key="index">
					<view class="text-centerp">
						<view class="cu-avatar xl book-shadow" :style="'width:160upx;height: 240upx;background-image:url('+item.goods_master_image+')'"></view>
					</view>
					<view class="text-black text-bold text-xl margin-top-sm text-center">
						{{item.goods_name}}
					</view>
					<view class="text-gray3 text-center text-sm">作者：{{item.author}}</view>
				</view>
			</scroll-view> -->
		</view>
		<cu-bar PageCur="home"> </cu-bar> 
	</view>
</template>

<script>
	var ysz_ico_actsch='data:image/png;base64,';
	var ysz_ico_fbook='data:image/png;base64,';
	var ysz_ico_guide='data:image/png;base64,';
	var ysz_ico_liveon='data:image/png;base64,';
	var ysz_liv_play='data:image/png;base64,';
	export default {
		data() {
			return {
				cardCur: 0,
				swiperList: [],
				dotStyle: true,
				towerStart: 0,
				direction: '',
				TabCur: 0,
				scrollLeft: 0,
				current:0,
				classList:[{id:0,name:'全部'}],
				bookList:[],
				bookPage:1,
				bookLast:0,
				bookLoad:'loading',
				likeBookList:[],
				liveList:[],
				livePage:1,
				liveLast:0,
				liveLoad:'loading',
				noticeCount:0,
				ActivityList:[],
				CustomBar:this.CustomBar,
				ysz_ico_actsch:ysz_ico_actsch,
				ysz_ico_fbook:ysz_ico_fbook,
				ysz_ico_guide:ysz_ico_guide,
				ysz_ico_liveon:ysz_ico_liveon,
				ysz_liv_play:ysz_liv_play
			};
		},
		onShow() {
			console.log(this.StatusBar)
		},
		onLoad() {
			this.getActivity();
			this.getBanner();
			this.getClass();
			this.getBook();
			//this.getLikeBook();
			this.getLive();
			this.getNotice();
		},
		methods: {
			getActivity(){
				let that=this;
				var data={
					page:1,
					pageSize:1
				}
				this.GetData('/seckill/get_seckill_list',data,function(res){
					if(res.code==200){
						that.ActivityList=res.data.data[0]||[];
					}
				},'post',function(){
					uni.stopPullDownRefresh();
				})
			},
			getNotice(){
				let that=this;
				var data={
					'page':1,
					'pageSize':1
				}
				this.GetData('/api/get_notice',data,function(res){
					that.noticeCount=res.data.total;
				},'post',function(){
					uni.stopPullDownRefresh();
				})
			},
			getClass(){
				let that=this;
				this.GetData('/api/get_goods_class_list',{},function(res){
					that.classList=that.classList.concat(res.data);
					 console.log(res);
				},'get',function(){
					uni.stopPullDownRefresh();
				})
			},
			getBanner(){
				let that=this;
				this.GetData('/api/get_banner',{name:'幻灯片'},function(res){
					that.swiperList=res.data.adv;
					 console.log(res);
				},'get',function(){
					uni.stopPullDownRefresh();
				})
			},
			getBook(){
				let that=this;
				let data={
					page:that.bookPage,
					pageSize:4,
					class_id:that.classList[that.TabCur].id,
				}
				that.bookLoad='loading';
				this.GetData('/goods/search_goods',data,function(res){
					that.bookList=res.data.data;
					if(res.data.current_page>=res.data.last_page){
						that.bookLast=1;
					}else if(res.data.current_page==1 && that.bookLast==1){
						that.bookLast=0;
					}
					if(res.data.total<=0){
						that.bookLoad='over';
					}else{
						that.bookLoad='change';
					}
					
				},'get',function(){
					uni.stopPullDownRefresh();
				})
			},
			getLikeBook(){
				let that=this;
				this.GetData('/goods/search_goods',{},function(res){
					that.likeBookList=res.data.data;
					console.log(2);
				},'get',function(){
					uni.stopPullDownRefresh();
				})
			},
			getLive(){
				let that=this;
				that.liveLoad='loading';
				this.GetData('/integral/goods/search_integral_goods',{page:that.livePage,pageSize:2},function(res){
					that.liveList=res.data.data;
					if(res.data.current_page>=res.data.last_page){
						that.liveLast=1;
					}else if(res.data.current_page==1 && that.liveLast==1){
						that.liveLast=0;
					}
					
					if(res.data.total<=0){
						that.liveLoad='over';
					}else{
						that.liveLoad='change';
					}
				},'get',function(){
					uni.stopPullDownRefresh();
				})
			},
			changeBook(){
				let that=this;
				if(that.bookLoad!=='change'){
					return;
				}
				if(that.bookLast==1){
					this.bookPage=1;
				}else{
					this.bookPage++;
				}
				this.getBook(); 
			},
			changeLive(){
				let that=this;
				if(that.liveLoad!=='change'){
					return;
				}
				if(that.liveLast==1){
					this.livePage=1;
				}else{
					this.livePage++;
				}
				this.getLive(); 
			},
			tabSelect(e) {
				this.bookPage=1;
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
				this.getBook();
			},
			toSearch(){
				uni.navigateTo({
					url:'../classify/search'
				})
			},
			swiperChange(e){
				this.current = e.detail.current;
			},
			toUrl(e){
				let url = e.currentTarget.dataset.url;
				uni.navigateTo({
					url:url
				})
			},
			adUrl(e){
				let advType = e.currentTarget.dataset.advtype;
				let advLink = Number(e.currentTarget.dataset.advlink);
				let id = e.currentTarget.dataset.id;
				
				console.log(advLink)
				if(this.isEmpty(advLink) || advLink<=0){
					return;
				}
				let url='';
				if(advType==0){
					url='/pages/activity/hall?id='+advLink
				}else if(advType==1){
					url='/pages/classify/detail?id='+advLink
				}else if(advType==2){
					url='/pages/press/detail?id='+advLink
				}else if(advType==3){
					url='/pages/notice/detail?id='+id
				}
				uni.navigateTo({
					url:url
				})
			}
		}
	}
</script>

<style>
	.page {
		height: 100vh;
	}

</style>
